import React, {Component} from 'react';
//import { Link } from 'react-router-dom';
import {
  Button,
  Card,
  CardBody,
  CardGroup,
  Col,
  Container,
  Form,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Row
} from 'reactstrap';

import {authenticate, makeAccessToken} from '../../../util/APIUtils';
import {ACCESS_TOKEN} from '../../../constants';

class Login extends Component {

  constructor(props) {
    super(props);

    this.state = {
      username: '',
      password: '',
      validate: {}
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  validate(e) {
    const { validate } = this.state
    if (e.target.value && e.target.value.trim().length > 0) {
      validate[e.target.name] = 'Y';
    } else {
      validate[e.target.name] = 'N';
    }
    this.setState({ validate })
  }

  handleChange = async (event) => {
    const { target } = event;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const { name } = target;
    await this.setState({
      [ name ]: value,
    });
  }

  handleSubmit(e) {
    e.preventDefault();

    let valid = true;
    const { validate } = this.state

    Object.keys(validate).forEach((key) => {
      if (validate[key] !== 'Y') {
        valid = false;
      }
    });

    if (!valid) {
      return;
    }

    authenticate({
      username: this.state.username,
      password: this.state.password
    }).then(response => {
      localStorage.setItem(ACCESS_TOKEN, makeAccessToken(this.state.username, response.token));
      this.props.onLogin();

      validate['error'] = 'N';
      this.setState({ validate })

      this.props.history.push("/");
    }).catch(error => {
      if(error.status === 401) {
      } else {
      }

      validate['error'] = 'Y';
      this.setState({ validate })
    });
  }

  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form onSubmit={ (e) => this.handleSubmit(e) }>
                      <h1>Login</h1>
                      <p className="text-muted">Sign In to your account</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-user"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" name="username" placeholder="Username" autoComplete="username"
                               value={ this.state.username }
                               valid={ this.state.validate.username === 'Y' }
                               invalid={ this.state.validate.username === 'N' }
                               onChange={ (e) => {
                                 this.validate(e)
                                 this.handleChange(e)
                               } } />
                        <FormFeedback invalid>
                          Please input a correct username.
                        </FormFeedback>
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" name="password" placeholder="Password" autoComplete="current-password"
                               value={ this.state.password }
                               valid={ this.state.validate.error === 'N' }
                               invalid={ this.state.validate.error === 'Y' }
                               onChange={this.handleChange} />
                        <FormFeedback invalid>
                          Can't login, please input correct username/password!
                        </FormFeedback>
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                          <Button color="primary" className="px-4">Login</Button>
                        </Col>
                        <Col xs="6" className="text-right">
                          {/*<Button color="link" className="px-0">Forgot password?</Button>*/}
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
{/*
                <Card className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                  <CardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua.</p>
                      <Link to="/register">
                        <Button color="primary" className="mt-3" active tabIndex={-1}>Register Now!</Button>
                      </Link>
                    </div>
                  </CardBody>
                </Card>
*/}
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Login;
